@use "./colors.scss" as *;

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: var(--bg-color);
  font-family: "Spartan", sans-serif;
  display: grid;
  place-items: center;
  transition: 300ms ease-in-out;
  min-height: 100vh;
}
