:root,
:root.blue {
  --bg-color: hsl(222, 26%, 31%);
  --keypad-color: hsl(223, 31%, 20%);
  --output-color: hsl(224, 36%, 15%);
  --keys-custom-bg: hsl(225, 21%, 49%);
  --keys-custom-shadow: hsl(224, 28%, 35%);
  --keys-equal: hsl(6, 63%, 50%);
  --keys-equal-shadow: hsl(6, 70%, 34%);
  --keys-bg: hsl(30, 25%, 89%);
  --keys-shadow: hsl(28, 16%, 65%);
  --text-color: hsl(221, 14%, 31%);
  --white: #ffffff;
}
:root h1,
:root span,
:root label,
:root.blue h1,
:root.blue span,
:root.blue label {
  color: var(--white);
}

:root.light {
  --bg-color: hsl(0, 0%, 90%);
  --keypad-color: hsl(0, 5%, 81%);
  --output-color: hsl(0, 0%, 93%);
  --keys-custom-bg: hsl(185, 42%, 37%);
  --keys-custom-shadow: hsl(185, 58%, 25%);
  --keys-equal: hsl(25, 98%, 40%);
  --keys-equal-shadow: hsl(25, 99%, 27%);
  --keys-bg: hsl(45, 7%, 89%);
  --keys-shadow: hsl(35, 11%, 61%);
  --text-color: hsl(60, 10%, 19%);
  --white: #ffffff;
}
:root.light h1,
:root.light span,
:root.light label {
  color: var(--text-color);
}

:root.custom {
  --bg-color: hsl(268, 75%, 9%);
  --keypad-color: hsl(268, 71%, 12%);
  --output-color: hsl(268, 71%, 12%);
  --keys-custom-bg: hsl(281, 89%, 26%);
  --keys-custom-shadow: hsl(285, 91%, 52%);
  --keys-equal: hsl(176, 100%, 44%);
  --keys-equal-shadow: hsl(177, 92%, 70%);
  --keys-bg: hsl(268, 47%, 21%);
  --keys-shadow: hsl(290, 70%, 36%);
  --text-color: hsl(52, 100%, 62%);
  --text-blue: hsl(
    198,
    20%,
    13%
  );
  --white: #ffffff;
}
:root.custom h1,
:root.custom span,
:root.custom label {
  color: var(--text-color);
}
:root.custom button[data-equals] {
  color: var(--text-blue);
}

@media (prefers-color-scheme: light) {
  :root {
    --bg-color: hsl(0, 0%, 90%);
    --keypad-color: hsl(0, 5%, 81%);
    --output-color: hsl(0, 0%, 93%);
    --keys-custom-bg: hsl(185, 42%, 37%);
    --keys-custom-shadow: hsl(
      185,
      58%,
      25%
    );
    --keys-equal: hsl(25, 98%, 40%);
    --keys-equal-shadow: hsl(25, 99%, 27%);
    --keys-bg: hsl(45, 7%, 89%);
    --keys-shadow: hsl(35, 11%, 61%);
    --text-color: hsl(60, 10%, 19%);
    --white: #ffffff;
  }
  :root h1,
:root span,
:root label {
    color: var(--text-color);
  }
}
@media (prefers-color-scheme: dark) {
  :root {
    --bg-color: hsl(222, 26%, 31%);
    --keypad-color: hsl(223, 31%, 20%);
    --output-color: hsl(224, 36%, 15%);
    --keys-custom-bg: hsl(225, 21%, 49%);
    --keys-custom-shadow: hsl(
      224,
      28%,
      35%
    );
    --keys-equal: hsl(6, 63%, 50%);
    --keys-equal-shadow: hsl(6, 70%, 34%);
    --keys-bg: hsl(30, 25%, 89%);
    --keys-shadow: hsl(28, 16%, 65%);
    --text-color: hsl(221, 14%, 31%);
    --white: #ffffff;
  }
  :root h1,
:root span,
:root label {
    color: var(--white);
  }
}
main {
  padding: 1.5rem;
  width: 100%;
  max-width: 33.5rem;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.5625rem;
}
header h1 {
  font-size: 1.75rem;
}
@media (min-width: 40em) {
  header h1 {
    font-size: 2.5rem;
  }
}
header .theme-changer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .theme-changer span {
  margin-right: 1.25rem;
  text-transform: uppercase;
  font-size: 0.75rem;
  letter-spacing: 0.125rem;
}
header .theme-changer .radio-field {
  margin: auto;
  position: relative;
  border-radius: 1.875rem;
  display: flex;
  width: 4.375rem;
  height: 1.5625rem;
  background: var(--keypad-color);
  outline: none;
  border: none;
}
header .theme-changer .radio-field input {
  position: absolute !important;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  width: 1px;
  border: 0;
  overflow: hidden;
}
header .theme-changer .radio-field label {
  position: absolute;
  font-size: 0.75rem;
  top: -1.25rem;
}
header .theme-changer .radio-field label:before {
  content: "";
  background: var(--keys-equal);
  width: 1rem;
  height: 1rem;
  position: absolute;
  top: 1.5rem;
  opacity: 0;
  border-radius: 50%;
  left: 50%;
  transform: translateX(-50%);
}
header .theme-changer .radio-field label:hover {
  cursor: pointer;
}
header .theme-changer .radio-field label:nth-child(2) {
  left: 12%;
}
header .theme-changer .radio-field label:nth-child(4) {
  left: 50%;
  transform: translateX(-50%);
}
header .theme-changer .radio-field label:nth-child(6) {
  right: 12%;
}
header .theme-changer .radio-field input:checked + label:before {
  box-shadow: none;
  opacity: 1;
}
header .theme-changer .radio-field input:focus + label:before {
  outline: 0.125rem solid var(--text-color);
}
header .theme-changer .radio-field input:focus + label {
  color: var(--keys-equal);
}

.output {
  margin-top: 1.875rem;
  width: 100%;
  min-height: 5.625rem;
  background: var(--output-color);
  border-radius: 0.625rem;
  display: flex;
  justify-content: flex-end;
}
.output > div {
  padding: 0.625rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  word-wrap: break-word;
  word-break: break-all;
  color: var(--white);
}
.output > div .previous-operand {
  font-size: 0.75rem;
  display: flex;
  align-self: flex-end;
}
.output > div .current-operand {
  font-size: 2.1875rem;
}

.input-container {
  background: var(--keypad-color);
  margin-top: 1.875rem;
  padding: 1.25rem;
  display: grid;
  grid-template-columns: repeat(4, minmax(3.75rem, auto));
  grid-template-rows: repeat(5, 3.75rem);
  grid-gap: 0.625rem;
  border-radius: 0.625rem;
}
@media (min-width: 40em) {
  .input-container {
    grid-gap: 1.25rem;
  }
}
.input-container .span-two {
  grid-column: span 2;
}
.input-container button {
  color: var(--text-color);
  background: var(--keys-bg);
  font-family: var(--font-family);
  font-size: 1.5rem;
  font-weight: bold;
  border-radius: 0.625rem;
  border: none;
  border-bottom: 0.3125rem solid var(--keys-shadow);
  cursor: pointer;
}
.input-container button[data-equals] {
  color: var(--white);
  background: var(--keys-equal);
  border-bottom: 0.3125rem solid var(--keys-equal-shadow);
  font-size: 0.75rem;
}
.input-container button[data-delete], .input-container button[data-reset] {
  color: var(--white);
  background: var(--keys-custom-bg);
  border-bottom: 0.3125rem solid var(--keys-custom-shadow);
  font-size: 0.75rem;
}
.input-container button:focus {
  outline: 0.125rem solid var(--keys-equal);
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: var(--bg-color);
  font-family: "Spartan", sans-serif;
  display: grid;
  place-items: center;
  transition: 300ms ease-in-out;
  min-height: 100vh;
}